@import url('../../fonts.css');

.header {
    height: auto;
    width: 100%;
    background-image: url('../../assets/images/background.svg');
    padding-bottom: 50px;
}

.header .hero {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 50px;
    gap: 10px;
    width: 80%;
    margin: auto;
}

.header .hero .hero-p {
    color: #fff;
    font-family: 'PoppinsRegular';
    font-size: 10px;
    letter-spacing: 2px;
}
.header .hero h3 {
    color: #fff;
    font-family: 'PoppinsRegular';
    letter-spacing: 2px;
    font-weight: 400;
    font-size: 2vh;
}
.header .hero .tagLine {
    width: 60%;
    padding: 10px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    text-align: center;
    color: #fff;
    font-size: 10px;
    font-family: 'PoppinsRegular';
    letter-spacing: 1px;
    text-transform: uppercase;
}
.header .hero .logo {
    width: 100%;
}
.header .hero .logo img {
    width: 100%;
}

.header .hero .hero-footer {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    align-items: center;
    width: 100%;
    color: #fff;
    font-size: 10px;
    font-family: 'PoppinsRegular';
    letter-spacing: 1px;
}
.header .hero .hero-footer .line {
    height: 1px;
    background-color: #fff;
    border: none;
    width: 13%;
}