.footer {
    width: 80%;
    background-color: transparent;
    padding-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 25px;
    justify-content: center;
    align-items: center;
    font-family: 'PoppinsRegular';
    color: #fff;
    font-size: 12px;
    letter-spacing: 2px;
    margin: auto;
    padding-bottom: 30px;
}

.footer .line {
    width: 100%;
    height: 1px;
    background-color: #fff;
    border: none;
}

.footer ul {
    list-style: none;
    display: flex;
    gap: 10px;
}
.footer ul li {
    height: 35px;
    width: 35px;
    border: 1px solid #fff;
    border-radius: 50%;
}

.footer .footer-text {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 10px;
    letter-spacing: 0;
}

.t-align-left {
    text-align: left;
    width: 100%;
    font-size: 10px;
    letter-spacing: 0;
}

.btn-community {
    -webkit-border-radius: 48px;
    -moz-border-radius: 48px;
    border-radius: 48px;
    color: #FFFFFF;
    font-size: 12px;
    font-weight: 100;
    padding: 25px;
    background-color: #93141D;
    border: solid #FFFFFF 1px;
    display: inline-block;
    cursor: pointer;
    text-align: center;
 }

 .footer ul li .contactIcon{
    align-items: center;
    width: 34px;
    height: 23px;
    margin-top: 5px;
}