.home {
    width: 80%;
    height:auto;
    margin: auto;
}

.t-align-center {
    text-align: center;
}

.home .info {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
}
.home .info div {
    width: 45%;
    color: #fff;
    height: auto;
    font-size: 11px;
    font-family: 'PoppinsRegular';
}
.home .assets {
    display: flex;
}

.home .assets img {
    width: 33%;
}

.home .membership {
    background-color: #fff;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 35px;
    height: auto;
}

.home .membership .initial {
    color: #606060;
    width: 80%;
    padding-top: 30px;
    font-size: 12px;
}

.home .membership .line {
    height: 1px;
    width: 40%;
    background-color: #C10019;
    border: none;
}

.home .membership .line-grey {
    height: 1px;
    width: 80%;
    background-color: #606060;
    border: none;
}

.home .membership .heading {
    color: #C10019;
    font-size: 18px;
}
.home .membership .middle {
    color: #606060;
    margin-top: 20px;
    font-size: 12px;
    text-decoration: underline;
}

.home .membership .middle-2 {
    color: #606060;
    font-size: 12px;
}

.home .membership .middle-3 {
    color: #606060;
    margin-top: 20px;
    font-size: 12px;
}